import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { useHistory, useParams } from 'react-router-dom';
// 추가 CSS를 미리 입혀놓은 컴포넌트 
import styled from 'styled-components';
import './Detail.scss';

let 박스 = styled.div`
padding:20px;
`;

let 제목 = styled.h4`
    font-size : 25px;
    color : ${props => props.색상}
`;

// // 라이프사이클에 따른 훅 설정
// class DetailCompo extends React.Component {
//     componentDidMount() {

//     }

//     componentWillUnmount() {

//     }
// }

function Detail(props) {

    // 11-1) 보이거나 안보이게 하는 상태값 저장 변수 생성 
    let [alertSoldOut, alertSoldOutVisible] = useState(true);
    // 12-1) 라이프사이클 테스트 용 상태값변경 변수
    let [inputText, inputTextSave] = useState('');

    // 라이프사이클 훅 (컴포넌트가 마운트 되었을때 컴포넌트가 업데이트 될때 특정 코드를 실행 할수 있음)
    useEffect(() => {
        console.log('detail page, useEffect');
        // 2초 후에 안보이게 하기 (변수에 적용)
        // let 타이머 = setTimeout(() => { }, 2000);

        // 10-1) function 만들거나
        // return function detailHide() { };

        // 10-2) 축소 function 으로 만들거나
        // return () => { }

        // 12-4) 업데이트 될때마다 useEffect(()=>{실행할 코드}) 실행 된다. 자원낭비. 그래서 조건을 설정한다. useEffect(()=>{실행할 코드},[조건])
        // 조건-> useState() 변경될때만 실행하도록 조건으로 설정.


        // 40-1) 페이지 첫 로딩시에만 서버에 데이터 요청할때, 조건은 비워둘것. (화면 업데이트시 요청 하지 않도록)
        // axios.get(URL);
        // useEffect(()=>{},[]); 조건을 빈칸으로.

        // 11-3) 표시 여부 상태값 변경
        let soldOutVisibleTimer = setTimeout(() => { alertSoldOutVisible(false) }, 2000);

        // 12-5) 타이머같은 경우 설정값이 지나기 전에 페이지가 바뀌면 에러 있음, 반드시 해제해줘야한다.
        return () => { clearTimeout(soldOutVisibleTimer) }
    }, [alertSoldOut]);


    // 10-3) 실행할것이 많을때는 useEffect(); 여러개 만들어 써도 됨, 위에서부터 차례대로 실행됨
    // useEffect(() => { });

    let { id } = useParams();
    let clickedProduct = props.stuff.find(
        function (product) {
            return product.id == id
        }
    );

    let count = props.stuffLeftCount[id];
    console.log("count: %d", count)

    // 뒤로가기 
    let history = useHistory();

    return (
        <div className='container'>
            <박스>
                <제목 className='red'>상세페이지</제목>
            </박스>

            {/* 12-2) 라이프사이클 테스트 */}
            <input onChange={(e) => { inputTextSave(e.target.value) }} />
            {/* 12-3) input 에 입력 되는 이벤트가 발생할때마다 업데이트 실행됨 */}
            {inputText}

            {
                // 11-2) 삼항연산자 사용하여 if 대신 실행
                alertSoldOut === true ? (
                    <div className="alert-sold-out">
                        <p>残り僅か!</p>
                    </div>
                ) : null // 아무것도 표시 하지 않을때는 null 
            }

            <div className='row'>
                <div className='col-md-6'>
                    <img src={clickedProduct.img} />
                </div>

                <div className='col-md-6 mt-4'>

                    <h4 className='pt-5'>{clickedProduct.title}</h4>
                    <p>{clickedProduct.content}</p>
                    <p>{clickedProduct.price} 원</p>

                    {console.log("stuffLeftCount:: %d", props.stuffLeftCount)}
                    <RemainInfo stuffLeftCount={count} ></RemainInfo>

                    <button className='btn btn-danger' onClick={() => {
                        // 40-3) state 사본 만들고, 사본 변경 후 사본을 변경함수에 집어 넣는다.
                        

                    }}>주문하기
                    </button>
                    &nbsp;
                    <button className='btn btn-danger' onClick={() => {
                        history.goBack();
                        // history.push('/')
                    }}>뒤로가기</button>
                </div>
            </div>
        </div >
    );
}


function RemainInfo(props) {
    console.log('RemainInfo, count: %d', props.stuffLeftCount);

    return (
        <p>재고 : {props.stuffLeftCount}</p>
    );
}


export default Detail;