import React, { useState } from "react";
import { Link } from 'react-router-dom';

function Product(props) {
    console.log('image: %s, %d', props.stuff.img, props.stuff.id)
    return (
        <div className='col-md-4'>
            <h3>{props.idx}</h3>
            <Link to={"/detail/" + props.stuff.id}>
                <img src={props.stuff.img} width="100%" /></Link>
            <h4>{props.stuff.title}</h4>
            <p>{props.stuff.content}</p>
            <p>{props.stuff.price}</p>
        </div>
    );
}

export default Product;